body {
  background: black;
  color: grey;
  text-align: left;
}
.App {
  display: flex;
  justify-content: center;
  align-items: center;
}

.calculator-container {
  background: white;
  width: 480px;
  border-radius: 15px;
  opacity: 0;
  transform: translate(-500px);
  transition: 0.2s;
  animation: anim 4s ease-in-out forwards;
  margin-top: 50px;
}

@keyframes anim {
  50% {
    opacity: 0.2;
    transform: translate(200px);
  }
  75% {
    opacity: 0.5;
    transform: translate(-100px);
  }
  100% {
    opacity: 1;
    transform: translate(0px);
  }
}

.row {
  display: flex;
}
