.button-container {
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  background-color: #585858;
  color: white;
  flex: 1;
  border-radius: 10px;
  box-shadow: 6px 3px 16px grey;
  padding: 10px;
  margin: 13px;
  transition: 0.3s ease-in-out;
}

.button-container:hover {
  background-color: rgb(78, 76, 76);
  scale: 1.1;
}
